
import { defineComponent } from "vue";

export default defineComponent({
  props: ["orderList"],
  emits: ["showItemDetail", "addToOrder"],
  methods: {
    showItemDetail(id: number) {
      this.$emit("showItemDetail", id);
    },
    addToOrder(id: number, qty: number) {
      this.$emit("addToOrder", { id, qty });
    },
  },
});
